import React, { Component } from 'react';
// import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Add extends Component {
    constructor() {
        super();
        //for heroku API
        // this.state = {
        //     name: '',
        //     email: '',
        //     password: ''
        // };


        //for local API
        this.state = {
            empName: '',
            empEmail: '',
            empDesg: '',
            empDept: ''
        }
        this.onSubmit = this.onSubmit.bind(this);
        this.dataChange = this.dataChange.bind(this);
        this.msgOnFill = this.msgOnFill.bind(this);
    }


    //This function will store form value in state.
    dataChange = (e) => {
        let state = this.state
        state[e.target.name] = e.target.value;
        this.setState(state);
    }

    onSubmit = (e) => {
        e.preventDefault();
        //for heroku API
        // let { name, email, password } = this.state;
        // axios.post('https://test-api-mauik.herokuapp.com/users', { name, email, password })
        //     .then((result) => {
        //         this.setState({
        //             name: '',
        //             email: '',
        //             password: ''
        //         })
        //     });

        //for local API
        let { empName, empDesg, empEmail, empDept } = this.state;
        axios.post('http://localhost:8000/emp', { empName, empEmail, empDept, empDesg })
            .then((result) => {
                console.log(result)
                this.setState({
                    empName: '',
                    empEmail: '',
                    empDesg: '',
                    empDept: ''
                })
            });

        //render to show - display users screen on submit click.
        this.props.history.push('/show')
    }


    //function to show an alert based on button click.
    msgOnFill() {
        //for heoku API
        // let { name, email, password } = this.state;
        // if (name !== '' && email !== '' && password !== '') {
        //     alert('User added sucessfully.')
        // }

        //for local API
        let { empName, empDesg, empEmail, empDept } = this.state;
        if (empName !== '' && empEmail !== '' && empDept !== '' || empDesg == '') {
            alert('Employee added sucessfully.')
        }


    }


    render() {
        //for heroku API
        // let { name, email, password } = this.state;

        //for local API
        let { empName, empDesg, empEmail, empDept } = this.state;
        return (
            //for heroku API
            // <div class="container">
            //     <div class="panel panel-default">
            //         <div class="panel-heading">
            //             <h3 class="panel-title">
            //                 Add New User
            //             </h3>
            //         </div>
            //         <div class="panel-body">
            //             <h4><Link to="/show"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Display Users</Link></h4>

            //             <form onSubmit={this.onSubmit}>
            //                 <div class="form-group">
            //                     <label for="name">Name:</label>
            //                     <input type="text" class="form-control" name="name" value={name} onChange={this.dataChange} placeholder="Enter Your Name" />
            //                 </div>
            //                 <div class="form-group">
            //                     <label for="email">Email:</label>
            //                     <input type="email" class="form-control" name="email" value={email} onChange={this.dataChange} placeholder="Enter Email Address" />
            //                 </div>
            //                 <div class="form-group">
            //                     <label for="password">Password:</label>
            //                     <input type="password" class="form-control" name="password" value={password} onChange={this.dataChange} placeholder="Enter Password" />
            //                 </div>

            //                 <button type="submit" disabled={!email || !name || !password} onClick={this.msgOnFill} class="btn btn-default">Submit</button>
            //             </form>
            //         </div>
            //     </div>
            // </div>


            //for local API
            <div class="container">
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <h3 class="panel-title">
                            Add New Employee
                    </h3>
                    </div>
                    <div class="panel-body">
                        <h4 ><Link to="/show"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Display Employees</Link></h4>

                        <form onSubmit={this.onSubmit}>
                            <div class="form-group">
                                <label for="empName">Employee Name:</label>
                                <input type="text" class="form-control" name="empName" value={empName} onChange={this.dataChange} placeholder="Enter Employee Name" />
                            </div>
                            <div class="form-group">
                                <label for="empEmail">Employee Email:</label>
                                <input type="email" class="form-control" name="empEmail" value={empEmail} onChange={this.dataChange} placeholder="Enter Employee Email Address" />
                            </div>
                            <div class="form-group">
                                <label for="empDesg">Designation:</label>
                                <input type="text" class="form-control" name="empDesg" value={empDesg} onChange={this.dataChange} placeholder="Enter Designation" />
                            </div>
                            <div class="form-group">
                                <label for="empDept">Department:</label>
                                <input type="text" class="form-control" name="empDept" value={empDept} onChange={this.dataChange} placeholder="Enter Employee Deprtment" />
                            </div>

                            <button type="submit" disabled={!empEmail || !empName || !empDesg || !empDept} onClick={this.msgOnFill} class="btn btn-default">Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}


export default Add;